@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  /* margin: 0; */
  /* padding: 0; */
  background-color: #111;
  color: #fff;
}

.lists {
  margin-top: -150px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

footer p span a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

footer p span a:hover {
  color: #C3181F;
  text-decoration: none;
}

.loading {
  position: fixed;  
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;

  
}
.movieRow {
margin-bottom: 20px;
}
.movieRow h2 {
margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all 0.8s ease;
}

.movieRow--item {
    display: inline-block;
    width: 150px;
}

.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all 0.2s ease-in-out;
}

.movieRow--list img:hover {
    transform: scale(1);
}


.movieRow--left,
.movieRow--right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;

}

.movieRow--left {
    left: 0;
}

.movieRow--right {
    right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
}

@media (max-width: 768px) {
    .movieRow--left,
    .movieRow--right {
        opacity: 1;
    }
}
.highlighted {
  height: 100vh;
}

.highlighted--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.highlighted--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.highlighted--name {
  font-size: 60px;
  font-weight: bold;
}

.highlighted--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.highlighted--points,
.highlighted--year,
.highlighted--seasons {
  display: inline-block;
  margin-right: 15px;
}

.highlighted--points {
  color: #43d369;
}

.highlighted--description {
  font-size: 20px;
  margin-top: 15px;
  color: #999;
  max-width: 40%;
}

.highlighted--buttons {
  margin-top: 15px;
}

.highlighted--watch-button,
.highlighted--list-button {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 15px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 12px;
  opacity: 1;
  transition: all ease 0.2s;
}

.highlighted--watch-button:hover,
.highlighted--list-button:hover {
  opacity: 0.7;
}

.highlighted--watch-button {
  background: #fff;
  color: #000;
}

.highlighted--list-button {
  background: #333;
  color: #fff;
}

.highlighted--genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width: 768px) {
  .highlighted {
    height: 90vh;
  }

  .highlighted--name {
    font-size: 40px;
  }

  .highlighted--info {
    font-size: 16px;
  }

  .highlighted--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }

  .highlighted--watch-button,
  .highlighted--my-list-button {
    font-size: 16px;
  }
  .highlighted--genres {
    font-size: 14px;
  }

}
header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}



header.black {
  background: #141414;
}

.header--logo {
  height: 25px;
}

.header--user {
  height: 35px;
}

.header--logo img {
  height: 100%;
}

.header--user img {
    height: 100%;
    border-radius: 3px ;
}

